import './src/utils/globals.css';

const addScript = url => {
  const script = document.createElement("script")
  script.src = url
  script.async = true
  document.head.appendChild(script)
}

export const onClientEntry = () => {
  window.onload = () => {
    addScript("https://stackpile.io/stack_162299.js")
  }
}
